@import "./variables";

html {
  font-size: 100%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

body {
  margin: 0px;
  padding: 0px;
  min-height: 100vh;
  font-family: "Poppins", sans-serif;
  background-color: $kentoBlue;
  color: white;
}

.App {
  display: grid;
  flex-direction: column;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.navbar {
  display: flex;
  justify-content: left;
  min-height: 6%;
  gap: 5vw;
  padding: 10px 10vw;
  font-size: 1.5rem;
  position: sticky;
  top: 0;
  background-color: $kentoBlue;
}

a {
  color: white;
  text-decoration: none;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.hero {
  font-size: 4rem;
  padding-left: 10vw;
  margin: 0;
  animation: fadeIn 3s;
}

.title-holder {
  margin-top: 15rem;
  justify-items: left;
  margin-right: 20px;
}

.name-title,
.job-title {
  margin: 0;
  padding: 0;
}

.yellow-text {
  color: $kentoYellow;
}

.about {
  background-color: $kentoOrange;
  display: flex;
  flex-direction: column;
  text-align: center;
  padding: 0 10vw;
}

.about h1 {
  font-size: 4rem;
}

.info {
  font-size: 1.5rem;
}

.skill-list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(175px, 1fr));
  grid-template-rows: repeat(3, 1fr);
  gap: 4rem;
  justify-items: center;
}

.skill-box {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 200px;
  max-width: 150px;
  border-radius: 10px;
  padding: auto;
}

.skill-box img {
  max-height: 120px;
  max-width: auto;
  filter: grayscale(10%);
  opacity: 50%;
  transition: all 0.2s ease-in-out;
}

.skill-box img:hover {
  opacity: 100%;
  filter: grayscale(0%);
  transform: scale(1.1);
}

.projects {
  font-size: 4rem;
  background-color: white;
  color: $kentoBlue;
  display: grid;
  grid-template-rows: 1fr 1fr;
  justify-items: center;
}

.contact {
  background-color: $kentoBlue;
  font-size: 2rem;
  display: grid;
  justify-items: center;
  padding-bottom: 100px;
  position: relative;
}

.contactBtns {
  display: flex;
  gap: 30px;
}

.contactBtns button {
  color: white;
  font-size: 2rem;
  min-height: 15vh;
  max-width: 15vh;
  border-radius: 50%;
  background-color: $kentoGreen;
  border: 10px solid white;
  cursor: pointer;
}

.contact-btn {
  max-height: 4rem;
  width: 4rem;
}
